import b2bFooter from "../../components/b2bfooter.vue";
export default({
	components:{
		b2bFooter 
	},
	
	data:function(){
		return {
			pageData:{},
			pageLoad:false,
			type:""
		}
	},
	onLoad:function(){
		this.getPage();
	},
	methods:{
		setType:function(type){
			this.type=type;
			this.getPage();
		},
		goBlog:function(id){
			uni.navigateTo({
				url:"../b2b_blog/show?id="+id
			})
		},
		getPage:function(){
			var that=this;
			that.app.get({
				url:that.app.apiHost+"/module.php?m=b2b_blog&a=list&ajax=1",
				data:{
					type:that.type
				},
				dataType:"json",
				success:function(res){
					that.pageLoad=true;
					that.pageData=res.data;
				}
			})
		}
	}
})
